@import "colors";
@import "typography";

//z-index values
$z-index-base: 1;
$z-index-between: 2;
$z-index-above: 10;
$z-index-behind: -1;
$z-index-hidden: -1000;

$border-radius-base: 0.5rem;
$border-radius-button: 2rem;

$card-shadow: 0 0.2rem 0.6rem rgba(0, 0, 0, 0.25);

$base-outline: 0.2rem solid $color-dark;

// breakpoints
$breakpoint-lg: 1230;
$breakpoint-md: 768;
$breakpoint-sm: 500;

// widths:
$actor-card-width: 17rem;
