@charset "utf-8";

@import "base/base";
@import "mixins/mixins";

@import "print";

*,
*::after,
*::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: $font-size-reset;
    scroll-behavior: smooth;

    @include respond-to($breakpoint-md, max) {
        font-size: $font-size-reset-md;
    }

    @include respond-to($breakpoint-sm, max) {
        font-size: $font-size-reset-sm;
    }
}

body {
    background-color: rgb(250, 248, 248);
    font-family: $font-family;
    font-size: $font-size-17;
    line-height: 1.3;
    overflow-x: hidden;
}

body,
header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
}

a {
    text-decoration: none;
    color: black;

    &:focus-visible {
        outline: $base-outline;
    }

    &:focus:not(:focus-visible) {
        outline: 0;
    }
}

ul,
li {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

button {
    cursor: pointer;
    font-family: $font-family;

    &:focus-visible {
        outline: $base-outline;
    }

    &:focus:not(:focus-visible) {
        outline: 0;
    }
}

.visually-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.loader {
    width: 100%;
    height: 80vh;
}
